import { useState } from "react";
import { Button, Form, Image, ListGroup, Tab } from "react-bootstrap";
import Search from "../../components/seach-box";
import "./index.scss";

function Messages() {
  const myID = 1;
  const chats = [
    {
      id: 1,
      receiver: "TOM 1",
      chats: [
        {
          id: 1,
          content: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, accusantium.",
        },
        {
          id: 2,
          content: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, accusantium.",
        },
        {
          id: 1,
          content: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, accusantium.",
        },
      ],
    },
    {
      id: 2,
      receiver: "TOM 2",
      chats: [
        {
          id: 2,
          content: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, accusantium.",
        },
        {
          id: 1,
          content: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, accusantium.",
        },
      ],
    },
    {
      id: 3,
      receiver: "TOM 3",
      chats: [
        {
          id: 1,
          content: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, accusantium.",
        },
        {
          id: 3,
          content: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, accusantium.",
        },
      ],
    },
  ];
  const [chatsList, setChatList] = useState(chats);
  const [message, setMessage] = useState<string>("");
  const [currentChat, setCurrentChat] = useState<any>(null);

  const handleTyping = function (e: any) {
    setMessage(e.target.value);
  };
  const onSend = function () {
    const newChatList = [...chatsList];
    let chat = newChatList.find(function (chat) {
      return chat.id === currentChat;
    });
    chat?.chats.push({
      id: myID,
      content: message,
    });
    setChatList(newChatList);
    setMessage("");
  };

  return (
    <div className="page-message">
      <Tab.Container id="list-group-tabs-example" defaultActiveKey="link1">
        <div className="d-flex">
          <div className="flex-item">
            <div className="page-message__list">
              <div className="d-flex justify-content-between direct-message">
                <span>
                  Direct Message
                  <Image src="./img/Path 86.svg" />
                </span>
                <span>
                  <Image src="./img/new message icon.svg" alt="new mess" />
                </span>
              </div>

              <div className="mt-2 mb-2">
                <Search />
              </div>
              <ListGroup variant="flush">
                {chatsList &&
                  chatsList.map(function (chat) {
                    return (
                      <ListGroup.Item
                        action
                        eventKey={"chat" + chat.id}
                        key={chat.id}
                        onClick={() => setCurrentChat(chat.id)}
                      >
                        <div className="list-item d-flex">
                          <div className="flex-grow-0 avatar">
                            <Image src="./img/0.png" width="44" height="44" />
                          </div>
                          <div className="flex-grow-1 ml-2">
                            <div className="d-flex justify-content-between">
                              <h6 className="name">{chat.receiver}</h6>
                              <span className="text-secondary recent-date">{"mess.date"}</span>
                            </div>
                            <div className="text-secondary width-200 text-truncate">
                              {chat.chats[chat.chats.length - 1].content}
                            </div>
                          </div>
                        </div>
                      </ListGroup.Item>
                    );
                  })}
              </ListGroup>
            </div>
          </div>
          <div className="flex-item message-detail">
            <Tab.Content>
              {chatsList &&
                chatsList.map(function (chat) {
                  return (
                    <Tab.Pane className="message-tab" eventKey={"chat" + chat.id}>
                      <div className="message-detail__info">
                        <div className="d-flex justify-content-between">
                          <div>
                            <Image src="./img/0.png" alt="avatar" />
                            <span className="message-detail__username">{chat.receiver}</span>
                          </div>
                          <span>
                            <Image src="./img/phone.svg" alt="phone" />
                          </span>
                        </div>
                      </div>
                      <div className="message-detail__content overflow-auto">
                        {chat.chats.map((mess) =>
                          mess.id === myID ? (
                            <div className="my-message text-right text-break  ">
                              <p className="d-inline-block bg-blue ml-5 mw-75 ">{mess.content}</p>
                            </div>
                          ) : (
                            <div className=" text-left ">
                              <Image src="./img/0.png" alt="avatar" />
                              <p>{mess.content}</p>
                            </div>
                          )
                        )}
                      </div>
                      <div className="message-detail__custom">
                        <div className="d-flex justify-content-between">
                          <div>
                            <Image src="./img/paperclip icon.svg" />
                          </div>
                          <div className="ml-4 w-100 ">
                            <Form.Control
                              as="textarea"
                              rows={1}
                              className="border-radius-20"
                              type="text"
                              placeholder="Start typing a message..."
                              onChange={handleTyping}
                              value={message}
                            />
                          </div>
                          <Button onClick={onSend} type="submit" className="btn-send">
                            Send
                          </Button>
                        </div>
                      </div>
                    </Tab.Pane>
                  );
                })}
            </Tab.Content>
          </div>
        </div>
      </Tab.Container>
    </div>
  );
}

export default Messages;
